const CustomObjectFieldTemplate = ({
  TitleField,
  title,
  description,
  properties,
  required,
  idSchema,
}) => {
  const isRoot = idSchema.$id === "root";
  return (
    <fieldset
      style={{
        border: isRoot ? "none" : "1px solid #ccc",
        padding: isRoot ? "0" : "10px",
        margin: isRoot ? "0" : "0 0 15px 0",
        borderRadius: isRoot ? "0" : "4px",
      }}
    >
      {!isRoot && title && <legend>{title}{required ? "*" : null}</legend>}
      {description && <p>{description}</p>}
      {properties.map((prop) => (
        <div key={prop.name}>{prop.content}</div>
      ))}
    </fieldset>
  );
};

