function findCaliper(weight, finish) {
  if (!weight || !finish) return null;
  return paperDatabase[weight]?.[finish] ?? null;
}

function calculateDigitalLift(B5, B6, caliper) {
  const B4 = caliper * 0.001;                // caliper in inches
  const B7 = B5 / B6;                        // number of sheets
  const C7 = B7 * B4;                        // stack height
  const B8 = Math.round((C7 / 3.6) * 10) / 10; // height ratio
  const C9 = B8 < 1 ? 1 : B8;                // lift factor
  const B10 = Math.min(B5, Math.ceil(B5 / C9 / B6) * B6); // records per lift
  return B10;
}

function calculateJpressLift(B5, B6, stockWeight) {
  const divisor = stockWeight.toLowerCase().includes("cover") ? 300 : 500;
  const B7 = B5 / B6;                        // sheets
  const B8 = Math.round((B7 / divisor) * 10) / 10; // ratio (not directly used in B10)
  const B10 = Math.min(B5, Math.floor((B6 * divisor) / B6) * B6); // records per lift
  return B10;
}


function Login({ onLogin }) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    if (username === "prep" && password === "frosty") {
      onLogin(username);
    } else {
      setError("Invalid username or password.");
    }
  };

  return (
    <div className="login-container">
      <h2>Please Log In</h2>
      <form onSubmit={handleSubmit} autoComplete="on">
        <div>
          <label htmlFor="username">Username</label>
          <input
            id="username"
            name="username"
            type="text"
            autoComplete="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
            autoFocus
          />
        </div>
        <div>
          <label htmlFor="password">Password</label>
          <input
            id="password"
            name="password"
            type="password"
            autoComplete="current-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        {error && <div className="login-error">{error}</div>}
        <button type="submit">Log In</button>
      </form>
    </div>
  );
}

function App() {
  const [formData, setFormData] = useState({});
  const [presetList, setPresetList] = useState([]);
  const [isLoadingPresets, setIsLoadingPresets] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [hasErrors, setHasErrors] = useState(false);
  const [lastErrors, setLastErrors] = useState([]);

  // Store natural image sizes for scaling and layout
  const [side1Size, setSide1Size] = useState({ width: 0, height: 0 });
  const [side2Size, setSide2Size] = useState({ width: 0, height: 0 });

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + "/templates.json")
      .then((res) => res.json())
      .then((data) => {
        const flattened = Object.values(data).flat();
        setPresetList(flattened);
        setIsLoadingPresets(false);
      })
      .catch((err) => {
        console.error("Failed to load templates.json", err);
        setPresetList([]);
        setIsLoadingPresets(false);
      });
  }, []);
  useEffect(() => {
    const pressType = formData.JobSettings?.PressType;
    const stockWeight = formData.LiftSettings?.StockWeight;
    const stockFinish = formData.LiftSettings?.StockFinish;
    const numRecords = formData.LiftSettings?.NumberOfRecords;
    const numUp = formData.LiftSettings?.NumberUp;
  
    if (!pressType || !stockWeight || !stockFinish || !numRecords || !numUp) return;
  
    const B5 = parseFloat(numRecords);
    const B6 = parseFloat(numUp);
    if (isNaN(B5) || isNaN(B6)) return;
  
    let liftValue = null;
    if (pressType === "JPress") {
      liftValue = calculateJpressLift(B5, B6, stockWeight);
    } else {
      const caliper = findCaliper(stockWeight, stockFinish);
      if (caliper != null) {
        liftValue = calculateDigitalLift(B5, B6, caliper);
      }
    }
  
    if (liftValue != null) {
      setFormData(prev => ({
        ...prev,
        LiftSettings: {
          ...prev.LiftSettings,
          LiftAmount: liftValue.toString(),
        },
      }));
    }
  }, [
    formData.JobSettings?.PressType,
    formData.LiftSettings?.StockWeight,
    formData.LiftSettings?.StockFinish,
    formData.LiftSettings?.NumberOfRecords,
    formData.LiftSettings?.NumberUp
  ]);
  
  const baseSchema = {
    title: "",
    type: "object",
    properties: {
      JobSettings: {
        type: "object",
        title: "Job Settings",
        properties: {
          JobFolderName: { type: "string", title: "Job Number" },
          PressType: { type: "string", title: "Press", enum: ["Indigo", "Ricoh", "JPress"] },
          ImposeType: {
            type: "string",
            title: "Imposition Type",
            default: "Cut & Stack",
            enum: [
              "Sequential",
              "Cut & Stack",
              "Label Sort",
              "Step & Repeat",
              "2u Saddle Stitch"
            ]
          }
        },
        required: ["JobFolderName","PressType"]
      },
  
      LiftSettings: {
        type: "object",
        title: "Lift Settings",
        properties: {
          StockWeight: {
            type: "string",
            title: "Stock Weight",
            enum: Object.keys(paperDatabase)
          },
          StockFinish: { type: "string", title: "Stock Finish", enum: [] },
          IsExplorerSilk: { type: "string", title: "Is it Explorer Silk?", enum: ["Yes", "No"] },
          NumberOfRecords: { type: "string", title: "Number of Records" },
          NumberUp: { type: "string", title: "Number Up" },
          LiftAmount: { type: "string", title: "Records Per Lift", default: "Default" }
        },
        required: ["LiftAmount", "StockFinish", "NumberOfRecords", "NumberUp", "StockWeight"]
        
      },
  
      SampleSettings: {
        type: "object",
        title: "Samples and Splitting",
        properties: {
          NeedsSampleOutput: { type: "string", title: "Needs Sample Output?", enum: ["Yes", "No"] },
          NeedsSampleWatermark: {
            type: "string",
            title: "Apply Sample Watermark?",
            enum: ["Yes", "No"],
            default: "Yes"
          },
          SampleStartRecord: { type: "string", title: "Sample Start Record", default: "Default" },
          SampleEndRecord: { type: "string", title: "Sample End Record", default: "Default" },
          SplitPDF: { type: "string", title: "Split PDF?", enum: ["Yes", "No"] },
          VariableSide: { type: "string", title: "What side is the variable on?", enum: ["Front", "Back"] }
        },
        required: ["NeedsSampleOutput", "SplitPDF",]
      },
  
      TemplateSection: {
        type: "object",
        title: "PDF Template",
        properties: {
          ImpositionTemplate: { type: "string", title: "Imposition Template" }
        },
        required: ["ImpositionTemplate"]
      }
    },
    required: ["JobSettings"]
  };


  const formSchema = useMemo(() => {

    const newProperties = { ...baseSchema.properties };
    const requiredFields = [...baseSchema.required];
  

    const sampleSettings = { ...newProperties.SampleSettings };
    const sampleProps = { ...sampleSettings.properties };
  

    if (formData.SampleSettings?.NeedsSampleOutput !== "Yes") {
      delete sampleProps.NeedsSampleWatermark;
      delete sampleProps.SampleStartRecord;
      delete sampleProps.SampleEndRecord;
    }
  

    if (formData.SampleSettings?.SplitPDF !== "Yes") {
      delete sampleProps.VariableSide;
    }
  

    sampleSettings.properties = sampleProps;
    newProperties.SampleSettings = sampleSettings;
  

    const liftSettings = { ...newProperties.LiftSettings };
    const liftProps = { ...liftSettings.properties };
    const selectedWeight = formData.LiftSettings?.StockWeight;
    liftProps.StockFinish.enum = selectedWeight && paperDatabase[selectedWeight]
      ? Object.keys(paperDatabase[selectedWeight])
      : [];
    liftSettings.properties = liftProps;
    newProperties.LiftSettings = liftSettings;

    const pressType = formData.JobSettings?.PressType;
const finish = (formData.LiftSettings?.StockFinish || "").trim().toLowerCase();

if (pressType !== "JPress" || finish !== "silk") {
  delete liftProps.IsExplorerSilk;
}

    return {
      ...baseSchema,
      properties: newProperties,
      required: requiredFields,
    };
  }, [
    formData.SampleSettings?.NeedsSampleOutput,
    formData.SampleSettings?.SplitPDF,
    formData.LiftSettings?.StockWeight,
    formData.LiftSettings?.StockFinish,
    formData.JobSettings?.PressType 
  ]);
  

  const uiSchema = {
    "ui:order": [
      "JobSettings",
      "LiftSettings",
      "SampleSettings",
      "TemplateSection"
    ],
  
    JobSettings: {
      PressType: { "ui:widget": "radio" },
      ImposeType: { "ui:widget": "select" }
    },
  
    LiftSettings: {
      StockWeight: {},
      StockFinish: {},
      IsExplorerSilk: { "ui:widget": "radio" },
      NumberOfRecords: {},
      NumberUp: {},
      LiftAmount: {}
    },
  
    SampleSettings: {
      NeedsSampleOutput: { "ui:widget": "radio" },
      NeedsSampleWatermark: { "ui:widget": "radio" },
      SplitPDF: { "ui:widget": "radio" },
      VariableSide: { "ui:widget": "radio" }
    },
  
    TemplateSection: {
      ImpositionTemplate: { "ui:widget": ImpositionTemplateWidget }
    }
  };
  

  // Validation: accept only "Default" or positive integer
  const validate = (formData, errors) => {
    const regexPositiveInt = /^[1-9]\d*$/;
    ["LiftAmount", "kStartRecord", "kEndRecord"].forEach((field) => {
      const val = formData[field];
      if (val !== "Default" && val !== undefined && !regexPositiveInt.test(val)) {
        errors[field].addError("Must be 'Default' or a positive integer");
      }
    });
    if (formData.NeedsSampleOutput === "Yes") {
      ["SampleStartRecord", "SampleEndRecord"].forEach((field) => {
        const val = formData[field];
        if (val !== "Default" && val !== undefined && !regexPositiveInt.test(val)) {
          errors[field].addError("Must be 'Default' or a positive integer");
        }
      });
    }
    return errors;
  };

  const handleSubmit = ({ formData }) => {
    if (hasErrors) {
      alert("Please fix the highlighted errors before submitting.");
      return;
    }
  
    // ✅ Get nested data
    const jobSettings = formData.JobSettings || {};
    const liftSettings = formData.LiftSettings || {};
    const sampleSettings = formData.SampleSettings || {};
    const templateSection = formData.TemplateSection || {};
  
    const JobFolderName = jobSettings.JobFolderName;
    const ImposeType = jobSettings.ImposeType;
    const ImpositionTemplate = templateSection.ImpositionTemplate;
  
    const {
      LiftAmount,
      NumberOfRecords,
      NumberUp,
      StockWeight,
      StockFinish,
      IsExplorerSilk
    } = liftSettings;
  
    const {
      NeedsSampleOutput,
      NeedsSampleWatermark = "No",
      SampleStartRecord,
      SampleEndRecord,
      SplitPDF = "",
      VariableSide = ""
    } = sampleSettings;
  
    // ✅ Derive PressType output
    let pressTypeOutput = jobSettings.PressType;
    if (pressTypeOutput === "JPress") {
      const finishLower = (StockFinish || "").toLowerCase();
      const isExplorer = IsExplorerSilk === "Yes";
  
      let stockClass = "";
      if (finishLower === "silk") {
        stockClass = isExplorer ? "Explorer Silk" : "Silk";
      } else if (["gloss", "matte", "dull"].some(f => finishLower.includes(f))) {
        stockClass = "Coated";
      } else if (["vellum", "smooth"].some(f => finishLower.includes(f))) {
        stockClass = "Uncoated";
      }
  
      const sideType = SplitPDF === "No" ? "Sheetwise" : "1 Sided";
  
      pressTypeOutput = `${stockClass}_${sideType}`;
    }
  
    const impositionTemplatePath = `DesignMerge_Workflows/ImpositionPresets/${ImpositionTemplate || ""}`;
  
    // ✅ Build output
    const output = {
      JSONID: "MPSAUTOMATION",
      Description: "MPS Properties",
      Settings: [
        {
          PressType: pressTypeOutput, // ✅ use derived press type
          NeedsSampleOutput,
          NeedsSampleWatermark,
          SampleStartRecord,
          SampleEndRecord,
          LiftAmount,
          ImposeType,
          ImpTemplatePath: impositionTemplatePath,
          SplitPDF,
          VariableSide,
          NumberUp,
          StockWeight,
          StockFinish
        }
      ]
    };
  
    const blob = new Blob([JSON.stringify(output, null, 2)], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `${JobFolderName || "Job"}_JobSettings.json`;
    a.click();
    URL.revokeObjectURL(url);
  };
  
  const getScaledStyle = ({ width, height }, maxSize = 1000) => {
    if (!width || !height) return {};
    if (width >= height) {
      const scale = maxSize / width;
      return { width: maxSize, height: height * scale };
    } else {
      const scale = maxSize / height;
      return { width: width * scale, height: maxSize };
    }
  };
  const selectedTemplate = formData.TemplateSection?.ImpositionTemplate || "";
  const isPreset = Array.isArray(presetList) && presetList.includes(selectedTemplate);
  const fullPath = `${BASE_PATH}${formData.JobFolderName || ""}`;

  if (!isAuthenticated) {
    return <Login onLogin={() => setIsAuthenticated(true)} />;
  }

  return (
    <div className="page-container">
      <div className="form-wrapper">
        <img
          src={process.env.PUBLIC_URL + "/2013_TSG_Logo_Outline.png"}
          alt="Logo"
          className="form-logo"
        />

        {isLoadingPresets ? (
          <div className="loading">Loading preset templates...</div>
        ) : (
          <>
            <Form
              schema={formSchema}
              uiSchema={uiSchema}
              formData={formData}
              validate={validate}
              onChange={(e) => {
                setFormData(e.formData);
                setHasErrors(false);
                setLastErrors([]);
              }}
              onSubmit={handleSubmit}
              onError={(errors) => {
                setHasErrors(true);
                setLastErrors(errors);
                // Build a detailed error message for alert
                const errorMessages = errors
                  .map((err) => {
                    const fieldName = err.property.replace(/^\.*/, "");
                    return `${fieldName}: ${err.message}`;
                  })
                  .join("\n");
                alert(`Please fix the following errors before submitting:\n${errorMessages}`);
              }}
              validator={validator}
              className="custom-form"
              ObjectFieldTemplate={CustomObjectFieldTemplate}   // ✅ Added this
            />

{formData.TemplateSection && formData.TemplateSection.ImpositionTemplate && (
  <div className="template-preview">
    <strong></strong>
    <div className="template-info">
      {(() => {
        const templateName = selectedTemplate;  // ✅ use selectedTemplate
        const isVerticalStack =
          side1Size.width > side1Size.height && side2Size.width > side2Size.height;
        return (
          <div
            className={
              "template-thumbnails " +
              (isVerticalStack ? "template-thumbnails-vertical" : "")
            }
          >
            <img
              key={`${templateName}-1`}
              src={
                isPreset
                  ? `${process.env.PUBLIC_URL}/presets/${templateName.replace(/\.pdf$/i, "")}-1.png`
                  : `${process.env.PUBLIC_URL}/jobs/${formData.JobFolderName}/${templateName.replace(/\.pdf$/i, "")}-1.png`
              }
              alt="Side 1 Preview"
              onError={(e) => (e.target.style.display = "none")}
              onLoad={(e) =>
                setSide1Size({
                  width: e.target.naturalWidth,
                  height: e.target.naturalHeight,
                })
              }
              style={getScaledStyle(side1Size)}
              className="template-thumb"
            />
            <img
              key={`${templateName}-2`}
              src={
                isPreset
                  ? `${process.env.PUBLIC_URL}/presets/${templateName.replace(/\.pdf$/i, "")}-2.png`
                  : `${process.env.PUBLIC_URL}/jobs/${formData.JobFolderName}/${templateName.replace(/\.pdf$/i, "")}-2.png`
              }
              alt="Side 2 Preview"
              onError={(e) => (e.target.style.display = "none")}
              onLoad={(e) =>
                setSide2Size({
                  width: e.target.naturalWidth,
                  height: e.target.naturalHeight,
                })
              }
              style={getScaledStyle(side2Size)}
              className="template-thumb"
            />
          </div>
        );
      })()}
    </div>
  </div>
)}
          </>
        )}
      </div>
    </div>
  );
}

export default App;
